import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  X,
  Info,
  AlertCircle,
  ArrowLeft,
  CheckCircle,
  User,
  Phone,
  Mail,
  MapPin,
  CreditCard,
  FileText,
  Clock
} from 'lucide-react';
// import PromoterNavbar removed - handled by ConditionalNavbar in App.js
import { useScrollAnimation, UnifiedBackground, UnifiedCard } from "../../common/components/SharedTheme";
import { useUnifiedToast } from '../../common/services/unifiedToastService';
import { useAuth } from '../../common/context/AuthContext';

export default function KYCFormPage() {
  const navigate = useNavigate();
  const { showSuccess, showError } = useUnifiedToast();
  useScrollAnimation();
  const { user } = useAuth();
  const [kycStatus, setKycStatus] = useState(null);
  const [loadingStatus, setLoadingStatus] = useState(true);

  const [kycDetails, setKycDetails] = useState({
    // Personal Information
    fullName: '',

    // Contact Information
    mobileNumber: '',
    email: '',

    // Address Information
    address: '',

    // Identity Information
    aadhaarNumber: '',
    panNumber: '',
  });

  React.useEffect(() => {
    if (user) {
      setKycDetails(prevDetails => ({
        ...prevDetails,
        // Personal Information
        fullName: user.name || '',

        // Contact Information
        mobileNumber: user.phone || '',
        email: user.email || '',

        // Address Information
        address: user.address || '',

        // Identity Information
        aadhaarNumber: user.aadhaarNumber || '',
        panNumber: user.panNumber || '',
      }));

      // Fetch current KYC status
      const fetchKycStatus = async () => {
        try {
          const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';
          const response = await fetch(`${apiUrl}/kyc/status/${user.id || user.promoter_id}`);
          if (response.ok) {
            const data = await response.json();
            setKycStatus(data);
          }
        } catch (err) {
        } finally {
          setLoadingStatus(false);
        }
      };
      fetchKycStatus();
    }
  }, [user]);

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    setKycDetails(prev => ({
      ...prev,
      [name]: files ? files[0] : value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Submit KYC for admin approval
      const kycSubmission = {
        promoter_id: user.id || user.promoter_id,
        promoter_name: kycDetails.fullName,
        email: kycDetails.email,
        phone: kycDetails.mobileNumber,
        address: kycDetails.address,
        aadhaar_number: kycDetails.aadhaarNumber,
        pan_number: kycDetails.panNumber,
        submitted_at: new Date().toISOString(),
        status: 'Pending'
      };

      // Call API to submit KYC for approval
      const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';
      const response = await fetch(`${apiUrl}/kyc/submit`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(kycSubmission)
      });

      if (!response.ok) {
        throw new Error('Failed to submit KYC');
      }

      const result = await response.json();

      // Show success message
      showSuccess('KYC submitted successfully! Your details are now pending admin approval.');

      // Redirect to profile after a short delay
      setTimeout(() => {
        navigate('/promoter/profile');
      }, 2000);

    } catch (error) {
      showError('Failed to submit KYC. Please try again.');
    }
  };

  const resetForm = () => {
    if (window.confirm('Are you sure you want to reset the form? All entered data will be lost.')) {
      setKycDetails({
        // Personal Information
        fullName: user?.name || '',

        // Contact Information
        mobileNumber: user?.phone || '',
        email: user?.email || '',

        // Address Information
        address: user?.address || '',

        // Identity Information
        aadhaarNumber: user?.aadhaarNumber || '',
        panNumber: user?.panNumber || '',
      });
    }
  };

  if (loadingStatus) {
    return (
      <UnifiedBackground>
        <div className="min-h-screen flex items-center justify-center">
          <div className="animate-spin rounded-full h-12 w-12 border-2 border-blue-400 border-t-transparent"></div>
        </div>
      </UnifiedBackground>
    );
  }

  // If KYC is already pending or approved, show status instead of form
  if (kycStatus && kycStatus.has_submission && (kycStatus.status === 'Pending' || kycStatus.status === 'Approved' || kycStatus.status === 'Verified')) {
    return (
      <UnifiedBackground>
        <div className="container mx-auto px-4 pt-48 pb-12 text-center">
          <UnifiedCard className="max-w-2xl mx-auto p-12">
            {kycStatus.status === 'Pending' ? (
              <>
                <div className="w-20 h-20 bg-yellow-500/20 rounded-full flex items-center justify-center mx-auto mb-6">
                  <Clock className="w-10 h-10 text-yellow-400 animate-pulse" />
                </div>
                <h2 className="text-3xl font-bold text-white mb-4">KYC Review in Progress</h2>
                <p className="text-gray-400 text-lg mb-8">
                  Your KYC documents have been submitted and are currently being reviewed by our administration team.
                  This process usually takes 24-48 hours.
                </p>
                <div className="space-y-4">
                  <div className="bg-gray-800/50 p-4 rounded-xl border border-gray-700">
                    <p className="text-sm text-gray-500 uppercase tracking-tighter mb-1">Status</p>
                    <p className="text-xl font-bold text-yellow-400">PENDING APPROVAL</p>
                  </div>
                  <button
                    onClick={() => navigate('/promoter/profile')}
                    className="w-full py-4 bg-gray-700 text-white rounded-xl hover:bg-gray-600 transition-colors font-bold"
                  >
                    Back to Profile
                  </button>
                </div>
              </>
            ) : (
              <>
                <div className="w-20 h-20 bg-green-500/20 rounded-full flex items-center justify-center mx-auto mb-6">
                  <CheckCircle className="w-10 h-10 text-green-400" />
                </div>
                <h2 className="text-3xl font-bold text-white mb-4">KYC Verified</h2>
                <p className="text-gray-400 text-lg mb-8">
                  Congratulations! Your account has been fully verified. You now have access to all system features.
                </p>
                <div className="space-y-4">
                  <div className="bg-green-500/10 p-4 rounded-xl border border-green-500/20">
                    <p className="text-sm text-green-500 uppercase tracking-tighter mb-1">Status</p>
                    <p className="text-xl font-bold text-green-400">VERIFIED</p>
                  </div>
                  <button
                    onClick={() => navigate('/promoter/profile')}
                    className="w-full py-4 bg-gradient-to-r from-green-600 to-emerald-600 text-white rounded-xl hover:from-green-700 hover:to-emerald-700 transition-colors shadow-lg shadow-green-500/20 font-bold"
                  >
                    Back to Profile
                  </button>
                </div>
              </>
            )}
          </UnifiedCard>
        </div>
      </UnifiedBackground>
    );
  }

  return (
    <UnifiedBackground>
      <div className="container mx-auto px-4 pt-48 pb-12">
        <UnifiedCard>
          <div className="p-6">
            <div className="flex items-center mb-8">
              <button
                type="button"
                onClick={() => navigate(-1)}
                className="mr-4 p-2 rounded-full hover:bg-gray-700 transition-colors"
              >
                <ArrowLeft className="w-5 h-5 text-gray-300" />
              </button>
              <div>
                <h1 className="text-2xl font-bold text-white">KYC Verification</h1>
                <p className="text-slate-400 mt-1">Complete your identity verification to unlock all features</p>
              </div>
            </div>

            <form id="kyc-form" onSubmit={handleSubmit} className="space-y-6" autoComplete="off">
              {/* Info Alert */}
              <div className="bg-blue-500/10 border border-blue-500/20 rounded-xl p-4 mb-6 flex items-start gap-3">
                <Info className="w-5 h-5 text-blue-400 mt-0.5 shrink-0" />
                <div className="text-sm text-blue-200/80">
                  <p className="font-semibold text-blue-300 mb-1">Why do I need KYC?</p>
                  <p>To comply with financial regulations and ensure the security of your account, we require identity verification before processing high-value transactions or withdrawals.</p>
                </div>
              </div>
              {/* Personal Information Section */}
              <div className="bg-gray-800 p-4 rounded-lg mb-6">
                <h4 className="text-lg font-semibold text-white mb-4 flex items-center">Personal Information</h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-300 mb-1">FULL NAME</label>
                    <div className="relative">
                      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <User className="h-5 w-5 text-gray-400" />
                      </div>
                      <input
                        type="text"
                        name="fullName"
                        value={kycDetails.fullName}
                        onChange={handleChange}
                        className="w-full pl-10 p-2 border border-gray-600 rounded-md bg-gray-700 text-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                        placeholder="Enter full name"
                        required
                      />
                    </div>
                  </div>
                </div>
              </div>

              {/* Contact Information Section */}
              <div className="bg-gray-800 p-4 rounded-lg mb-6">
                <h4 className="text-lg font-semibold text-white mb-4 flex items-center">Contact Information</h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">MOBILE NUMBER</label>
                    <div className="relative">
                      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Phone className="h-5 w-5 text-gray-400" />
                      </div>
                      <input
                        type="tel"
                        name="mobileNumber"
                        value={kycDetails.mobileNumber}
                        onChange={handleChange}
                        className="w-full pl-10 p-2 border border-gray-600 rounded-md bg-gray-700 text-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                        placeholder="Enter mobile number"
                        required
                      />
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">EMAIL ADDRESS</label>
                    <div className="relative">
                      <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Mail className="h-5 w-5 text-gray-400" />
                      </div>
                      <input
                        type="email"
                        name="email"
                        value={kycDetails.email}
                        onChange={handleChange}
                        className="w-full pl-10 p-2 border border-gray-600 rounded-md bg-gray-700 text-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                        placeholder="Enter email address"
                        required
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div className="bg-gray-800 p-4 rounded-lg mb-6">
                <h4 className="text-lg font-semibold text-white mb-4 flex items-center">Address Information</h4>
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-1">FULL ADDRESS</label>
                  <input
                    type="text"
                    name="address"
                    value={kycDetails.address}
                    onChange={handleChange}
                    className="w-full p-2 border border-gray-600 rounded-md bg-gray-700 text-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter full address"
                    required
                  />
                </div>
              </div>

              {/* Identity Information Section */}
              <div className="bg-gray-800 p-4 rounded-lg mb-6">
                <h4 className="text-lg font-semibold text-white mb-4 flex items-center">Identity Information</h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">AADHAAR NUMBER</label>
                    <input
                      type="text"
                      name="aadhaarNumber"
                      value={kycDetails.aadhaarNumber}
                      onChange={handleChange}
                      className="w-full p-2 border border-gray-600 rounded-md bg-gray-700 text-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      placeholder="Enter 12-digit Aadhaar number"
                      pattern="[0-9]{12}"
                      maxLength="12"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">PAN NUMBER</label>
                    <input
                      type="text"
                      name="panNumber"
                      value={kycDetails.panNumber}
                      onChange={handleChange}
                      className="w-full p-2 border border-gray-600 rounded-md bg-gray-700 text-white uppercase focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      placeholder="Enter 10-character PAN number (e.g., ABCDE1234F)"
                      pattern="[A-Z]{5}[0-9]{4}[A-Z]{1}"
                      maxLength="10"
                      required
                    />
                  </div>
                </div>
              </div>

              {/* Form Actions - Moved to bottom to avoid Navbar overlap */}
              <div className="flex flex-col md:flex-row items-center justify-end gap-4 pt-8 border-t border-gray-700/50 mt-8">
                <button
                  type="button"
                  onClick={resetForm}
                  className="w-full md:w-auto px-6 py-3 text-sm font-medium text-gray-300 bg-gray-800 border border-gray-700 rounded-xl hover:bg-gray-700 transition-all duration-200"
                >
                  Reset Form
                </button>
                <button
                  type="submit"
                  className="w-full md:w-auto px-10 py-3 text-sm font-medium text-white bg-gradient-to-r from-blue-600 to-purple-600 rounded-xl hover:from-blue-700 hover:to-purple-700 transition-all duration-300 shadow-lg shadow-blue-500/20 font-bold uppercase tracking-wider"
                >
                  Submit KYC for Approval
                </button>
              </div>
            </form>
          </div>
        </UnifiedCard>
      </div>
    </UnifiedBackground>
  );
}
